import { Time } from '@angular/common';

export class BaseEntity {
    id: string;
    version: number;
    createdDate: number;
    lastModifiedDate: number;
    createBy: string;
    lastModifiedBy: string;
}

export interface User {
    uuid: string;
    email: string;
    displayName: string;
    emailVerified: boolean;
 }

export class Idea extends BaseEntity {
    constructor() {
        super();
        this.name = '';
        this.notes = '';
    }
    name: string;
    notes: string;
}

export class Task extends BaseEntity {
    solarCalendar: Date;
    lunarCalendar: Date;
    caseName: string;
    location: string;
    hall: string;
    event: string;
    jobContent: JobContent;
    receiveTime: Time;
    CasketingTime: Time;
    moveCoffinTime: Time;
    worshipTime: Time;
    staff: Staff[];
    remarks: string;
    leader: string;
    status: string;
    notes: string;
}

export class JobContent {
    funeral: boolean; // 出殯
    funeralWorkerCount: number;
    inOut: boolean; // 出入現
    inOutWorkerCount: number;
}

export class Staff {
    name: string;
}
